.btn_container{
  flex: 1 1;
  height: 83px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 38px;
  background-color: #1b1b32;
  color: white;
  border-radius: 25px;
  border: 2px solid white;
  margin: 4px;
  cursor: pointer;
  user-select: none;
}

.btn_container:hover{
  background-color: #5a01a7;
}

.operator{
  background-color: #00471b;
}

.operator:hover{
  background-color: #f60;
}